/************** ItemViewer **************/

#itemViewerPage {
    width: 100%;
    max-width: var(--max-width);
    min-width: 320px;
    margin: auto;
    transition: bottom 250ms, opacity 250ms;
  }
  #itemViewerHeaderButtonsContainer {
    position: relative;
    width: 100%;
    height: 175px;
    background-color: var(--second-color);
  }
  #itemViewerHeader {
    position: sticky;
    top: 0;
    display: grid;
    grid-template-columns: repeat(2, 1fr) 0.4fr;
    grid-template-rows: auto;
    align-items: center;
    direction: ltr;
    padding: 20px;
    background-color: var(--second-color);
    z-index: 1;
  }
  #itemViewerHeaderTitle {
    color: white;
    margin: 0;
    font-size: calc(18px + 6 * ((var(--vw) - 320px) / 680));
    font-weight: bold;
    text-align: end;
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }
  #itemViewerBackButton {
    fill: white;
    width: 35px;
    height: 35px;
    cursor: pointer;
  }
  #itemViewerBackButton:hover {
    fill: black;
  }
  #itemViewerFavouriteButton {
    fill: var(--text-white);
    width: 35px;
    height: 35px;
    justify-self: end;
    cursor: pointer;
  }
  #itemViewerEdit {
    fill: white;
    width: 35px;
    height: 35px;
    justify-self: end;
    cursor: pointer;
  }
  #itemViewerEdit:hover {
    fill: black;
  }
  #itemViewerTrashCan {
    fill: white;
    width: 35px;
    height: 35px;
    margin-left: 10%;
    cursor: pointer;
  }
  #itemViewerTrashCan:hover {
    fill: black;
  }
  #itemViewerHeaderItemName {
    position: absolute;
    bottom: 20px;
    right: 20px;
    color: var(--text-white);
    margin: 0;
    font-weight: bold;
    font-size: calc(25px + 6 * ((var(--vw) - 320px) / 680));
    width: 75%;
  }
  #itemViewerOkButton {
    fill: white;
    width: 40px;
    height: 40px;
    cursor: pointer;
  }
  #itemViewerOkButtonBg {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    bottom: -30px;
    left: 20px;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    background-color: var(--main-color);
  }
  #itemViewerOkButtonAnimation1 {
    position: absolute;
    bottom: -30px;
    left: 20px;
    width: 60px;
    height: 60px;
    border: solid 1.5px var(--main-color);
    border-radius: 50%;
    box-sizing: border-box;
    pointer-events: none;
    animation: PulsAnimation 4s ease 1s infinite normal forwards;
  }
  #itemViewerOkButtonAnimation2 {
    position: absolute;
    bottom: -30px;
    left: 20px;
    width: 60px;
    height: 60px;
    border: solid 1.5px var(--main-color);
    border-radius: 50%;
    box-sizing: border-box;
    pointer-events: none;
    animation: PulsAnimation 4s ease 1.3s infinite normal forwards;
  }
  #itemViewerInputsContainer {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    background-color: var(--background-white);
    padding-top: 50px;
    padding-bottom: 25px;
  }
  #itemViewerSelectServing {
    width: 65%;
    height: 40px;
    color: var(--text-kinda-black);
    font-size: 18px;
    font-weight: bold;
    background-color: var(--background-white);
    border: none;
    border-radius: 0;
    border-bottom: solid 1px var(--text-kinda-black);
  }
  .itemViewerSelectServingItems {
    color: var(--text-kinda-black);
  }
  #itemViewerInput {
    padding: 0;
    width: 15%;
    height: 40px;
    color: var(--text-kinda-black);
    background-color: var(--background-white);
    font-size: 18px;
    font-weight: bold;
    text-align: center;
    border: none;
    border-radius: 0;
    border-bottom: solid 1px var(--text-kinda-black);
  }
  #itemViewerSelectMeal {
    width: 87%;
    height: 40px;
    margin: auto;
    margin-bottom: 25px;
    color: var(--text-kinda-black);
    font-size: 18px;
    font-weight: bold;
    background-color: var(--background-white);
    border: none;
    border-radius: 0;
    border-bottom: solid 1px var(--text-kinda-black);
  }
  #itemViewerKcal {
    display: flex;
    align-items: baseline;
    color: var(--text-kinda-black);
    font-size: 30px;
    font-weight: bold;
    margin: 0;
    margin-bottom: 20px;
    padding: 0px 7% 25px 0;
  }
  #itemViewerKcal span {
    font-size: calc(15px + 6 * ((var(--vw) - 320px) / 680));
    font-weight: normal;
    padding-right: 10px;
  }
  
  #itemViewerNutritionInfoTitle {
    color: var(--text-kinda-black);
    margin: 0;
    padding: 20px 20px 0 0;
    background-color: var(--background-white);
    font-weight: bold;
    font-size: calc(15px + 6 * ((var(--vw) - 320px) / 680));
  }
  #itemViewerNutritionInfoSubTitle {
    color: var(--text-kinda-black);
    margin: 0;
    padding-right: 20px;
    background-color: var(--background-white);
    font-size: calc(12px + 6 * ((var(--vw) - 320px) / 680));
  }
  
  #nutritionInfoPercentContainer {
    display: flex;
    justify-content: space-evenly;
    padding: 45px 0;
    background-color: var(--background-white);
  }
  .percentTitles {
    text-align: center;
    margin: 0;
    color: var(--text-kinda-black);
  }
  #itemViewerNutritionInfoDetail {
    display: flex;
    justify-content: space-between;
    padding: 0 20px;
    padding-bottom: 20px;
    background-color: var(--background-white);
  }
  .itemViewerNutritionInfoMain {
    font-weight: bold;
    font-size: calc(15px + 6 * ((var(--vw) - 320px) / 680));
    color: var(--text-kinda-black);
  }
  .itemViewerNutritionInfoSub {
    font-size: calc(14px + 6 * ((var(--vw) - 320px) / 680));
    margin-top: 2px;
    margin-bottom: 2px;
    color: var(--text-kinda-black);
  }
  #itemViewerNutritionInfoValues p {
    text-align: left;
  }
  @media (max-width: 450px) {
    #itemViewerBackButton {
      width: 30px;
      height: 30px;
    }
    #itemViewerFavouriteButton {
      width: 30px;
      height: 30px;
    }
    #itemViewerEdit {
      width: 30px;
      height: 30px;
    }
    #itemViewerTrashCan {
      width: 30px;
      height: 30px;
    }
    #itemViewerOkButton {
      width: 30px;
      height: 30px;
    }
    #itemViewerOkButtonBg {
      width: 50px;
      height: 50px;
      bottom: -25px;
    }
    #itemViewerOkButtonAnimation1 {
      width: 50px;
      height: 50px;
      bottom: -25px;
    }
    #itemViewerOkButtonAnimation2 {
      width: 50px;
      height: 50px;
      bottom: -25px;
    }
  }
  